body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.mygrid{display:grid;grid-template-columns:repeat(6, 1fr);grid-template-rows:auto;grid-gap:16px;margin:16px;margin-top:96px}@media screen and (max-width: 1272px){.mygrid{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 960px){.mygrid{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 500px){.mygrid{grid-template-columns:repeat(1, 1fr)}}.card{width:calc(100% - 32px);min-height:250px;border-radius:8px 8px 8px 8px;-moz-border-radius:8px 8px 8px 8px;-webkit-border-radius:8px 8px 8px 8px;border:2px solid #575757;padding:16px;display:grid}.card p{font-size:16px;font-weight:400;color:#575757;margin:0;white-space:pre-wrap}.card h1{text-align:center;margin:0;-webkit-align-self:center;align-self:center}.card button{border-radius:8px 8px 8px 8px;-moz-border-radius:8px 8px 8px 8px;-webkit-border-radius:8px 8px 8px 8px;border:2px solid #575757;padding:8px;min-width:50%;height:48px;justify-self:center}textarea{width:100%;height:100%;outline:none;resize:none;border-color:white;font-size:16px;font-weight:400;color:#575757}.myheader{position:fixed;top:0;height:64px;width:100vw;background-color:#353639;color:white;padding:4px;padding-left:16px;display:-webkit-flex;display:flex;text-align:center;box-shadow:2px 4px 8px 0px rgba(0,0,0,0.08)}.myheader h2{font-weight:500;font-size:24px;margin:16px}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

